body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-inter: Inter;
  --font-mulish: Mulish;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-11xl: 30px;
  --font-size-xl: 20px;
  --font-size-6xl: 25px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;
  --font-size-14xl: 33px;

  /* Colors */
  --color-white: #fff;
  --color-purple-100: #800080;
  --color-purple-200: #5f155f;
  --color-black: #000;
  --color-darkgray: #aaa;
  --color-lavender: #f0ecf9;
  --color-plum: #b2a2d7;

  /* border radiuses */
  --br-xl: 20px;
}
